.flickerOn {
  -webkit-animation: flicker 2s linear;
          animation: flicker 2s linear;
}
@-webkit-keyframes flicker {
  0% {
    opacity: 0;
  }
  69% {
    opacity: 0.22;
  }
  70% {
    opacity: 0.55;
  }
  75% {
    opacity: 0;
  }
  80% {
    opacity: 0.55;
  }
  85% {
    opacity: 0%;
  }
  90% {
    opacity: 0.22;
  }
}
@keyframes flicker {
  0% {
    opacity: 0;
  }
  69% {
    opacity: 0.22;
  }
  70% {
    opacity: 0.55;
  }
  75% {
    opacity: 0;
  }
  80% {
    opacity: 0.55;
  }
  85% {
    opacity: 0%;
  }
  90% {
    opacity: 0.22;
  }
}

.gradient-text {
  background: linear-gradient(45deg, #d36100, yellow);
  background-size: 150% 150%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}


.modal-content {  transition: border-color 0.2s linear;

  background: #222;
  padding: 2rem;
  background-repeat: no-repeat;
  background-size: 100% 3px;
  box-shadow: none;
  color: #000;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 16px;
  padding: 0.45rem 0;
  width: 100%;
  text-align: center;
  color: #ffdd9e;
 
}
.closeButton {
  color: white !important;
}

.modal-header {
  background-image: linear-gradient(to right, #ffe600 0, #ff7101 60%);
  background-repeat: no-repeat;
  background-size: 100% 3px;
  box-shadow: none;
  border-bottom: 3px solid orange;
  color: #000;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 20px;
  padding: 0.45 rem 0;
  transition: border-color 0.2s linear;
  width: 100%;
  text-align: center;
  color: #ffdd9e;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*Navbar styles below*/

.btn-light.mm {
  border: none;
  color: #ffdd9e;
  background: transparent;
  font-size: 17px;
  font-weight: 600;
  font-family: inherit;
  text-transform: lowercase;
  letter-spacing: 1.5px;
  width: 18vw;
  padding: 0px;
  margin: 0px 0px 0px 0px;
}

.btn-light.mm:hover:active:focus {
  outline: none !important;
  box-shadow: none !important;
}

.navbar {
  width: 100%;
  max-width: 100%;
  min-height: 10vh;
  position: fixed;
  top: 0;
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav-ul {
  width: 30%;
  height: 7vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  /*background-color: rgba(255, 255, 255, 0.1);*/
  /*backdrop-filter: blur(3px)*/
}

.nav-li {
  list-style-type: none;
}

.nav-li a {
  position: relative;
  font-size: 17px;
  font-weight: 600;
  color: #ffdd9e;
  font-family: inherit;
  text-decoration: none;
  text-transform: lowercase;
  letter-spacing: 1.5px;
  margin-left: 30px;
}
.nav-li a::after {
  content: "";
  position: absolute;
  bottom: -1.6vh;
  left: 0;
  height: 3px;
  width: 0%;
  background: linear-gradient(45deg, #d36100, yellow);
  background-size: 100% 2px;

  transition: 0.3s ease;
}
.nav-li a:hover::after {
  width: 100%;
}
.sr-only {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.mobile-toggle {
  display: none;
}

/*Navbar styles ended*/

.container-circle {
  position: relative;
  line-height: 50px;
  text-align: center;
}

.container-circle .circle {
  height: 200px;
  width: 200px;
  margin: 30px;
  background: linear-gradient(45deg, #d36100, #882e99a6);
  border-radius: 50%;
  text-align: center;
  display: inline-block;
}

