.input-matic {
  background-color: transparent;
  background-image: linear-gradient(to right, #ffe600 0, #ff7101 50%);
  background-repeat: no-repeat;
  background-position: 0 calc(100% + 3px), 0 0;
  background-size: 100% 3px;
  box-shadow: none;
  border: 0;
  border-bottom: 3px solid #5a4709;
  margin-top: 5px;
  color: #000;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  font-size: 20px;
  line-height: 1.5;
  outline: 0 none;
  padding: 0.25rem 0;
  transition: border-color 0.2s linear;
  width: 300px;
  text-align: center;
  color: #ffdd9e;
  background-color: rgba(53, 53, 53, 0.5);
}

.input-matic:hover,
.input-matic:focus {
  border-color: transparent;
}

